@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;1,400;1,800&display=swap);
body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* theming */
:root {
  /* colors */
  --white: #fff;
  --grey: #ccc;
  --black: #000;

  /* theme colors */
  --navbar-background-color: transparent;
  --navbar-scroll-background-color: var(--black);
  --logo-color: var(--white);
  --logo-outline-color: var(--black);
  --footer-background: var(--black);
  --footer-text: var(--white);
  --nav-bar-links-button-color: var(--grey);
}

@use './src/assets/colors'

.landing-page{background-color:var(--primary-color)}

.navbar{position:fixed;z-index:1;display:flex;width:100%;height:50px;align-items:center;justify-content:space-between;background-color:var(--navbar-background-color);transition:background-color 500ms}.navbar__logo{padding:0 16px;font-size:24px;text-transform:uppercase;color:var(--logo-color);font-weight:800;text-shadow:-1px -1px 0 var(--logo-outline-color),1px -1px 0 var(--logo-outline-color),-1px 1px 0 var(--logo-outline-color),1px 1px 0 var(--logo-outline-color)}.navbar__links{position:relative}.navbar__toggle-links-button{position:relative;padding:0;display:flex;background:transparent;outline:none;border:none;cursor:pointer;font-size:24px;height:24px;width:24px;margin-right:24px}.navbar__toggle-links-button-icon{display:flex;position:absolute;left:0;top:0;color:var(--nav-bar-links-button-color);width:100%;-webkit-backface-visibility:hidden;backface-visibility:hidden;transition:transform 500ms ease}.navbar__toggle-links-button-icon--hide{transform:rotateY(180deg)}.navbar__link-dropdown{position:absolute;padding:0;left:-120px;width:150px;border:1px solid #ccc;background-color:white}.navbar__link-item{width:100%;list-style:none}.navbar__link{display:block;width:100%;padding:8px;color:black}.navbar__link:hover{color:#ccc}

.footer{margin-top:48px;padding:24px 0;background-color:var(--footer-background);color:var(--footer-text)}

.minecraft-coordinate{display:flex;margin-bottom:96px;justify-content:space-between;background-color:#fff;border:1px solid #ccc}.minecraft-coordinate--image-left{flex-direction:row-reverse}@media only screen and (max-width: 600px){.minecraft-coordinate{flex-direction:column;margin-bottom:48px}}.minecraft-coordinate__text{width:100%;padding:12px}.minecraft-coordinate__image{display:flex;width:600px;height:320px;background-size:cover;background-repeat:no-repeat;background-position:50% 0px;flex-shrink:0}@media only screen and (max-width: 1000px){.minecraft-coordinate__image{width:480px;height:280px}}@media only screen and (max-width: 600px){.minecraft-coordinate__image{width:100%;margin-top:24px}}

.youtube-video{position:relative;height:100%;width:100%}.youtube-video__iframe{position:absolute;top:0;left:0;width:100%;height:100%}

.media-with-description{display:flex;margin-bottom:96px;justify-content:space-between;background-color:#fff;border:1px solid #ccc}.media-with-description--media-left{flex-direction:row-reverse}@media only screen and (max-width: 600px){.media-with-description{flex-direction:column;margin-bottom:48px}}.media-with-description__description{width:50%;flex-grow:0;padding:12px}@media only screen and (max-width: 600px){.media-with-description__description{width:100%}}.media-with-description__media{display:flex;width:600px;height:320px}@media only screen and (max-width: 1000px){.media-with-description__media{width:480px;height:280px}}@media only screen and (max-width: 600px){.media-with-description__media{width:100%;margin-top:24px}}.media-with-description__image{height:100%;width:100%;background-size:cover;background-repeat:no-repeat;background-position:50% 0px}

.main-body{display:flex;justify-content:center;align-items:center}.main-body__content{max-width:800px;min-height:800px;padding:0 16px}

.minecraft-page{background-color:var(--primary-color)}.minecraft-page__description{margin-bottom:96px}@media only screen and (max-width: 600px){.minecraft-page__description{margin-bottom:48px}}

.rocket-league-page__description{margin-bottom:96px}@media only screen and (max-width: 600px){.rocket-league-page__description{margin-bottom:48px}}

